
import PageSearch from '@/components/page-search'
import { ElTree, ElMessage, ElMessageBox } from 'element-plus'
import { useRouter } from 'vue-router'
import { useStore } from '@/store'
import { searchFormConfig } from '../config/search.config'
import { defineComponent, computed, ref, nextTick } from 'vue'
import { customerStyleList, activeFlag, creditList } from '@/constant/option'
import PageContent from '@/components/page-content'
import { usePageModal } from '@/hooks/use-page-modal'
import { menuMapLeafKeys } from '@/utils/map-menus'
import { useI18n } from 'vue-i18n'
import { modalConfig } from '../config/modal.config'
import ImportDialog from '../../../../base-ui/importdialog'
import { PageInfo } from '../edit/types/index'
export default defineComponent({
  name: 'main',
  components: {
    ImportDialog,
    PageContent,
    PageSearch
  },
  setup() {
    const { t } = useI18n()
    const router = useRouter()
    const importConfig = ref({
      tip: t('jpg/png files with a size less than 500kb'),
      href: '/ex'
    })
    let importDialogVisible = ref(false)
    const customerStyleArray = computed(() => customerStyleList)
    const activeFlagArray = computed(() => activeFlag)
    const creditArray = computed(() => creditList)
    const handleSizeChange = (val: number) => {
      pageInfo.value.pageSize = val
      searchInfo()
    }
    const handleRow = (rowData: any, label: any) => {
      if (label === 'Edit') {
        router.push({
          path: '/main/customer/newCustomer',
          query: rowData
        })
      }
      if (label === 'Delete') {
        ElMessageBox.confirm(t('deleteChange'), t('deleteTips'), {
          distinguishCancelAndClose: true,
          confirmButtonText: t('confirm'),
          cancelButtonText: t('cancel'),
          customClass: 'message-box',
          center: true
        })
          .then(() => {
            ElMessage({
              type: 'success',
              message: t('deleteSuccessful')
            })
          })
          .catch((action: any) => {
            ElMessage({
              type: 'info',
              message: action === 'cancel' ? t('deleteFailed') : t('canceled')
            })
          })
      }
      console.log(rowData, label, 'row父')
    }
    const selectionRow = (row: any) => {
      console.log(row, '父组件选择')
    }
    const beforeUpload = (file: any) => {
      if (file.type !== 'image/jpeg') {
        ElMessage.error('Avatar picture must be JPG format!')
        return false
      } else if (file.size / 1024 / 1024 > 2) {
        ElMessage.error('Avatar picture size can not exceed 2MB!')
        return false
      }
      return true
    }
    const handleSuccess = (file: any) => {
      console.log(file, '父filesuccess')
    }
    const handleBtn = (label: any) => {
      if (label === t('import')) {
        importDialogVisible.value = true
        console.log(importDialogVisible.value, 'importDialogVisible1')
      }
      console.log(label, '1111')
    }
    const contentTableConfig = {
      propList: [
        {
          prop: 'name',
          label: t('customerName'),
          minWidth: '100'
        },
        {
          prop: 'type',
          label: t('customerStyle'),
          minWidth: '100',
          type: 'select',
          option: customerStyleArray
        },
        {
          prop: 'openCredit',
          label: t('openCredit'),
          minWidth: '100',
          type: 'select',
          option: creditArray
        },
        {
          prop: 'activeFlag',
          label: t('activeFlag'),
          type: 'select',
          minWidth: '100',
          option: activeFlagArray
        }
      ],
      showIndexColumn: false,
      showSelectColumn: true,
      childrenProps: {
        rowKey: 'id',
        treeProp: {
          children: 'children'
        }
      },
      border: false,
      showFooter: true,
      isOperation: true,
      operation: {
        label: t('operation'),
        width: '200',
        cols: [
          {
            label: t('edit'),
            type: 'primary',
            handleRow: handleRow
          },
          {
            label: t('delete'),
            type: 'danger',
            handleRow: handleRow
          },
          {
            label: t('open'),
            type: 'danger',
            handleRow: handleRow
          }
        ]
      },
      totalCount: 12
    }
    const btnConfig = {
      isActions: true,
      addBtn: true,
      actions: [
        {
          label: t('import'),
          type: 'primary',
          style: 'plain',
          handleBtn: handleBtn
        },
        {
          label: t('export'),
          type: 'primary',
          style: 'plain',
          handleBtn: handleBtn
        }
      ]
    }
    const tableData = ref([
      {
        name: '王小虎',
        type: 1,
        url: 'http:baidu.com',
        activeFlag: 1,
        openCredit: 2,
        startTime: '1676263246000'
      },
      {
        name: '王1',
        type: 2,
        url: 'http:baidu.com',
        activeFlag: 2,
        openCredit: 1,
        startTime: '1678682446000'
      }
    ])
    const store = useStore()
    const otherInfo = ref({})
    const menus = computed(() => store.state.entireMenu)
    // 1.处理pageModal的hook
    const elTreeRef = ref<InstanceType<typeof ElTree>>()
    const editCallback = (item: any) => {
      const leafKeys = menuMapLeafKeys(item.menuList)
      nextTick(() => {
        console.log(elTreeRef.value)
        elTreeRef.value?.setCheckedKeys(leafKeys, false)
      })
    }
    const [defaultInfo, handleNewData, handleEditData] = usePageModal(
      undefined,
      editCallback
    )
    const handleQueryClick = (val: any) => {
      console.log(val, '查询query')
    }
    const handleAddClick = (val: any) => {
      console.log(val, 'valadd')
      router.push({ name: 'customerEdit', path: '/main/customer/edit' })
    }
    const searchInfo = () => {
      const params = Object.assign(
        {},
        {
          pageSize: pageInfo.value.pageSize,
          currentPage: pageInfo.value.currentPage
        }
      )
      console.log(params, 'paramsmain')
    }
    // const [handleResetClick] = usePageSearch()
    const handleCheckChange = (data1: any, data2: any) => {
      const checkedKeys = data2.checkedKeys
      const halfCheckedKeys = data2.halfCheckedKeys
      const menuList = [...checkedKeys, ...halfCheckedKeys]
      otherInfo.value = { menuList }
    }
    const handleCurrentChange = (val: number) => {
      pageInfo.value.currentPage = val
      console.log(val, 'manin父')

      searchInfo()
    }
    const pageInfo = ref<PageInfo>({
      totalCount: 100,
      pageSizes: [10, 20, 50, 100],
      handleSizeChange: handleSizeChange,
      handleCurrentChange: handleCurrentChange,
      currentPage: 1,
      pageSize: 20
    })

    return {
      customerStyleArray,
      handleQueryClick,
      searchFormConfig,
      handleNewData,
      defaultInfo,
      modalConfig,
      handleEditData,
      menus,
      otherInfo,
      handleCheckChange,
      elTreeRef,
      contentTableConfig,
      tableData,
      handleRow,
      t,
      activeFlagArray,
      creditArray,
      handleBtn,
      selectionRow,
      btnConfig,
      handleAddClick,
      importDialogVisible,
      ImportDialog,
      beforeUpload,
      handleSuccess,
      importConfig,
      ElMessage,
      pageInfo,
      searchInfo,
      handleCurrentChange
    }
  }
})
